// Reset
*, *::before, *::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

// Base
body, html {
    font-family: Roboto, sans-serif;
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-flow: column;
}

a{
    text-decoration: none;
    color : #000000;
}

.restaurant-header{
    margin-bottom:-55px;
    @media screen and (min-width: 1440px){
        margin-bottom: -40px;

    }
}

.header{
    width: 100%;
    display: flex;
    flex-flow: column nowrap;
    position:relative;

    &__title-container{
        height: 63px;
        display: flex;
        justify-content: center;
        align-items: center;
        box-shadow: 0px 2px 4px 0px #00000026;
        z-index: 50;

        @media screen and (min-width: 1440px){
            height: 96px;
        }


        & .header__title{
            font-size:30px;
            font-weight: 400;
            line-height: 43.74px;
            letter-spacing: 0;
            color : #101010;
            font-family: Shrikhand, Roboto, sans-serif;
            @media screen and (min-width: 1440px){
                height: 34.2px;
                font-size: 34.2px;

            }
        }


    }
    &__location{
        width: 100%;
        height: 50px;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #eaeaea;
        box-shadow: 0px 4px 4px 0px #00000040;
        z-index: 40;
        color: #353535;
        & p {
            font-size: 16px;
            font-weight: 500;
            line-height: 18.75px;
            letter-spacing: 0;

        }

        & .header__icon-location{
            width: 12px;
            height: 16 px;
            margin-right: 17px;
        }

        @media screen and (min-width: 1440px){


        }
    }
    &__go-back{
        position: absolute;
        width: 30px;
        height: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
        top :17px;
        left: 17px;
        color : #353535;
        z-index: 100;
        @media screen and (min-width: 1440px){
            top: 33px;
            left: 17px;
        }
    }

    &__img{
        width: 100%;
        height: 275px;
        overflow: hidden;
        object-fit: cover;
        & img{
            width: 100%;
            height: 100%;

        }
        @media screen and (min-width: 1440px){
            height: 383px;
        }
    }
}

.main-content{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #ffffff;
    flex-flow: column nowrap;
    max-width: 1440px;
    width: 100%;

    & main{
        width: 100%;
    }

    & .reserver{
        width: 100%;
        display: flex;
        flex-flow: column nowrap;
        justify-content: flex-start;
        align-items:center;
        background-color: #f6f6f6;
        padding : 39px 30px 57px 30px;
        &__title{
            font-size:24px;
            font-weight: 700;
            line-height: 28.13px;
            letter-spacing: 0;
            color : #000000;
            text-align: center;
            width: 275px;
            height: 68px;
            @media screen and (min-width: 1440px){
                line-height: 46.88px;
                font-size: 40px;
                width: auto;
                height: auto;
                margin-bottom: 18px;
                height: 47px;

                br{
                    display: none;
                }
            }
        }

        &__description{
            font-size:18px;
            font-weight: 300;
            line-height: 21.09px;
            letter-spacing: 0;
            color: #353535;
            text-align: center;
            width: 315px;
            height:46px;
            margin-bottom:27px;

            @media screen and (min-width: 1440px){
                height: 46px;
                width: auto;
                margin-bottom: 18px;
                vertical-align: auto;
                display: flex;
                align-items: center;

                br{
                    display: none;
                }
            }
        }

        &__ancre{
            width: 218px;
            height: 50px;
            border-radius: 25px;
            background: linear-gradient(0deg, #9356DC -11.44%, #FF79DA 123.93%);
            box-shadow: 0px 4px 10px 0px #00000040;
            font-weight: 500;
            font-size: 16px;
            line-height: 18.75px;
            letter-spacing: 0;
            text-align: center;
            text-decoration: none;
            color: #FFFFFF;
            padding: 16px;
            transition : filter 0.5s, box-shadow 0.5s;
            &:hover{
                background: linear-gradient(0deg, #9356DC -5.2%, #FF79DA 110.74%);
                filter: brightness(115%);
                box-shadow: 0px 4px 15px 0px #00000059;
                span {
                    display: inline-block;
                    animation: textMove 0.5s ease-in-out;
                }
            }
        }

        @media screen and (min-width: 1440px){
            padding: 30px 0;
        }
    }

    & .fonctionnement{
        width: 100%;
        display: flex;
        flex-flow: column nowrap;
        justify-content: center;
        align-items: flex-start;
        background-color: #ffffff;
        padding: 48px 20px 68px 20px;
        gap:26px;

        &__title{
            font-size:24px;
            font-weight: 700;
            line-height: 28.13px;
            letter-spacing: 0;
            color : #000000;
            height: 31px;
            width: 100%;
            max-width: 1024px;
        }

        &__list{
            display: flex;
            flex-flow: column nowrap;
            justify-content: flex-start;
            align-items: center;
            gap: 24.93px;
            width: 100%;
            padding-left: 12px;
            list-style: none;
            counter-reset: item;

            .fonctionnement__item{
                width: 100%;
                border-radius: 20px;
                background-color: #f6f6f6;
                box-shadow: 0px 4px 15px 0px #00000026;
                position: relative;
                padding: 28.59px 38px 28.12px 73.49px;
                font-size: 16px;
                font-weight: 500;
                line-height: 18.75px;
                counter-increment: item;
                color: #000000;

                & i{
                    position: absolute;
                    top: 50%;
                    transform: translateY(-50%);
                    border-radius: 50%;
                    color: #7E7E7E;
                    left: 20px;
                }
                & .fa-mobile-screen-button{
                    width: 13.02px;
                    height: 19.9px;
                    font-size: 19.9px;
                    left: 33.47px;
                }

                & .fa-list-ul{
                    width: 16px;
                    height: 15.92px;
                    font-size: 15.92px;
                    left: 34.49px;
                }

                & .fa-store{
                    width: 18px;
                    height: 17.91px;
                    font-size: 17.91px;
                    left: 35.49px;
                    color: #9356DC;
                }

                &::after{
                    content: counter(item);
                    position: absolute;
                    left: 0;
                    top: 50%;
                    font-size: 24px;
                    color: #FFFFFF;
                    width: 24px;
                    height: 24px;
                    font-size: 14.18px;
                    font-weight: 500;
                    line-height: 16.62px;
                    letter-spacing: 0;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    background-color: #9356DC;
                    border-radius: 100%;
                    transform: translate(-50%, -50%);
                }
            }
            @media screen and (min-width: 1440px){
                flex-flow: row nowrap;
                max-width: 1024px;
            }
        }
        @media screen and (min-width: 1440px){
            padding: 50px 0;
            gap:25px;
            justify-content: flex-start;
            align-items: center;
        }
    }

    & .restaurants{
        background-color: #f6f6f6;
        padding : 54px 17px;
        display: flex;
        flex-flow: column nowrap;
        gap:20px;
        &__title{
            font-size: 24px;
            font-weight: 700;
            line-height: 28.13px;
            letter-spacing: 0;
            color : #000000;
            width: 100%;
        }
        &__container{
            display: flex;
            flex-flow: column nowrap;
            align-items: center;
            gap: 18px;
            @media screen and (min-width: 1440px){
                gap:100px;
                row-gap: 50px;
                flex-flow: row wrap;
            }
        }

        .restaurant-card{
            position:relative;
            width: 100%;
            border-radius: 20px;
            background-color: #fff;
            overflow: hidden;
            max-width: 346px;
            @media screen and (min-width: 1440px){
                    width: 490px;
                    max-width: 490px;
            }

            &__article{
                display: flex;
                flex-flow: column nowrap;
                gap: 0;
                width: 100%;
            }
            &__container-img{
                width: 100%;
                height: 174px;
                overflow: hidden;
                position:relative;
            }
            &__image{
                width: 346px;
                height: 231px;
                position:absolute;
                bottom: 0;
                left: 50%;
                transform: translateX(-50%);
                object-fit: cover;
                @media screen and (min-width: 1440px){
                    width: 498.65px;
                    height: 231px;
                }
            }
            &__content{
                padding : 16px 25px 12px 13px;
                display: flex;
                flex-flow: row nowrap;
                gap: 10px;
                row-gap: 5px;
                justify-content: space-between;
                align-items: center;
                @media screen and (min-width: 1440px){
                    padding-left: 18.74px;

                }
            }

            &__texts{
                row-gap: 5px;
                display: flex;
                flex-flow: column nowrap;
            }

            &__title{
                font-size: 18px;
                font-weight: 700;
                line-height: 21.09px;
                letter-spacing: 0;
                color : #000000;
                height: 20px;

            }

            &__location{
                font-size: 17px;
                font-weight: 300;
                line-height: 19.92px;
                letter-spacing: 0;
                color : #000000;
                height: 24px;
            }

            &__new-tag{
                position:absolute;
                top:13px;
                right:12px;
                background-color: #99E2D0;
                color:#008766;
                font-weight: 500;
                font-size: 14px;
                line-height: 16.41px;
                letter-spacing: 0;
                text-align: center;
                width: 80px;
                height: 29px;
                box-shadow: 0px 2px 4px 0px #00000026;
                border-radius: 2px;
                display: flex;
                justify-content: center;
                align-items: center;
            }
        }
        @media screen and (min-width: 1440px){
            padding: 30px 180px;
            gap:28px;
        }
    }

    & .menu{
        background-color: #f6f6f6;
        border-radius: 40px 40px 0 0;
        display: flex;
        flex-flow: column nowrap;
        padding-bottom: 40px;
        z-index: 1000;
        &__header{
            padding: 32px 17px 40px 17px;
            display: flex;
            flex-flow: row nowrap;
            justify-content: space-between;
            align-items: center;
            width: 100%;
            &-title{
                font-size: 28px;
                font-weight: 400;
                line-height: 40.82px;
                letter-spacing: 0;
                color : #101010;
                font-family: Shrikhand, Roboto, sans-serif;
                @media screen and (min-width: 1440px){
                    margin-right: 24px;

                }
            }
            & .fa-heart{
                margin-right: 24px;
            }
            @media screen and (min-width: 1440px){
                justify-content: center;
                padding: 51px 0 40px 0;

            }
        }
        &__section{
            padding: 0 13px;
            margin-bottom: 40px;
            opacity: 0;
            animation: fadeIn 1s ease-in forwards;
            &:nth-child(2) {
                animation-delay: 0.5s;
            }

            &:nth-child(3) {
                animation-delay: .75s;
            }

            &:nth-child(4) {
                animation-delay: 1.25s;
            }
            &-header{
                margin-bottom: 12px;
                &-title{
                    font-family: Roboto;
                    font-weight: 300;
                    font-size: 16px;
                    line-height: 18.75px;
                    letter-spacing: 0%;
                    text-transform: uppercase;
                    &::after{
                        content: "";
                        display: block;
                        width: 40px;
                        height: 3px;
                        background-color: #99E2D0;
                        margin-top: 5.25px;
                    }
                }

            }

            &-content{
                display: flex;
                flex-flow: column nowrap;
                gap: 12px;

            }
            @media screen and (min-width: 1440px){
                max-width: 634px;
                width: 634px;
            }
        }

        &__dish{
            height: 69px;
            width: 100%;
            box-shadow: 0px 4.01px 15.04px 0px #0000001A;
            border-radius: 15.04px;
            overflow: hidden;
            position: relative;
            &-container{
                padding: 13.5px 15px;
                transition: padding-right 500ms;
                width: 100%;
                &-title{
                    font-family: Roboto;
                    font-weight: 500;
                    font-size: 18px;
                    line-height: 21.09px;
                    letter-spacing: 0%;
                    color: #000000;
                    margin-bottom: 5px;
                    text-overflow: ellipsis;
                    overflow: hidden;
                    white-space: nowrap;
                }
                &-content{
                    display: flex;
                    flex-flow: row nowrap;
                    justify-content: space-between;
                    gap: 5px;
                    &-description{
                        font-family: Roboto;
                        font-weight: 300;
                        font-size: 15.04px;
                        line-height: 17.63px;
                        letter-spacing: 0%;
                        color:#000000;
                        height: 19px;
                        text-overflow: ellipsis;
                        overflow: hidden;
                        white-space: nowrap;
                    }
                    &-price{
                        font-family: Roboto;
                        font-weight: 700;
                        font-size: 15.04px;
                        line-height: 17.63px;
                        letter-spacing: 0%;
                        text-align: center;
                        color:#353535;
                    }
                }
            }
            &-anim{
                width: 59px;
                height: 69px;
                background-color: #99E2D0;
                display: flex;
                justify-content: center;
                align-items: center;
                border-radius: 0 15px 15px 0;
                left:100%;
                position: absolute;
                top: 0;
                transition-property: transform;
                transition-duration: 500ms;
            }
            &:hover{
                .menu__dish-anim{
                    transform: translateX(-100%);
                }
                .menu__dish-container{
                    padding-right:74px;
                }
            }
        }

        &__order{
            display: flex;
            flex-flow: row nowrap;
            justify-content: center;
            &-btn{
                width: 186px;
                height: 50px;
                border-radius: 25px;
                background: linear-gradient(0deg, #9356DC -11.44%, #FF79DA 123.93%);
                box-shadow: 0px 4px 10px 0px #00000040;
                font-family: Roboto;
                font-weight: 500;
                font-size: 16px;
                line-height: 18.75px;
                letter-spacing: 0%;
                text-align: center;
                color: #FFFFFF;
                border: none;
                transition: filter 0.5s, box-shadow 0.5s;
                &:hover{
                    filter: brightness(115%);
                    box-shadow: 0px 4px 15px 0px #00000059;
                    background: linear-gradient(0deg, #9356DC -5.2%, #FF79DA 110.74%);
                    span {
                        display: inline-block;
                        animation: textMove 0.5s ease-in-out;
                    }
                }
            }
        }
        @media screen and (min-width: 1440px){
            max-width: 1055px;
            justify-content: center;
            align-items: center;
        }
    }
}


.footer{
    background-color: #353535;
    display: flex;
    flex-flow: column nowrap;
    width: 100%;
    padding: 22px 20px;
    gap: 16px;

    &__logo{
        font-weight: 400;
        font-size: 18px;
        line-height: 26.24px;
        letter-spacing: 0;
        font-family: Shrikhand, Roboto, sans-serif;
        color: #ffffff;
    }

    &__nav{
        display: flex;
        flex-flow: column nowrap;
        justify-content: space-between;
        align-items: self-start;
        gap: 7px;

        &-link-logo{
            font-weight: 500 !important;
            @media screen and (min-width: 1440px){
                width: 190px !important;
                height: 19px !important;

            }

        }
        &-link{
            height: 20px;
            font-size: 16px;
            font-weight: 400;
            line-height: 18.75px;
            letter-spacing: 0;
            color: #FFFFFF;
            @media screen and (min-width: 1440px){
                width: 130px;

            }

        }

        &-icon{
            height: 13px;
            width: 11px;
            line-height: 13px;
            font-size: 13px;
            margin-right: 11px;
            @media screen and (min-width: 1440px){
                font-size: 11px;

            }
        }
        @media screen and (min-width: 1440px){
            flex-flow: row nowrap;
            gap: 7px;

        }
    }
    @media screen and (min-width: 1440px){
        flex-flow: row-reverse nowrap;
        justify-content: flex-start;
        align-items: center;
        padding: 64px 25px 30px 599px;
    }
}

.fa-heart {
    width: 22px;
    height: 22px;
    color: black;
    font-size: 22px;
    position: relative;
    transition: color 1s ease-in-out;

    &::after {
        content: "\f004"; // Code Unicode pour l'icône de cœur
        font-family: "Font Awesome 5 Free";
        font-weight: 900;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: linear-gradient(0deg, #9356DC -11.44%, #FF79DA 123.93%);
        background-clip: text;
        -webkit-background-clip: text;
        color: transparent;
        clip-path: circle(0% at 50% 50%);
        transition: clip-path 1s ease-in-out;
    }

    &:hover::after {
        clip-path: circle(100% at 50% 50%);
    }

    &:hover {
        color: transparent;
    }
}

.loader {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(193.33deg, #9356DC -11.44%, #FF79DA 123.93%);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 9999;
    animation: pulse 2s ease-in-out, fadeOut 1s linear 1s forwards, hideLoader 0s linear ;
}

.loader__logo {
    font-family: 'Shrikhand', sans-serif;
    font-size: 3rem;
    color: #ffffff;
    animation: pulse 2s ease-in-out;
}

@keyframes pulse {
    0% {
        transform: scale(1);
    }
    25% {
        transform: scale(1.1);
    }
    50% {
        transform: scale(1);
    }
    100% {
        transform: scale(10);
    }
}

@keyframes fadeOut {
    to {
        opacity: 0;
        visibility: hidden;
    }
}

@keyframes hideLoader {
    to {
        display: none;
    }
}

@keyframes fadeIn {
    0% {
        opacity: 0;
        transform: translateY(30px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes textMove {
    0%, 100% {
        transform: translate(0, 0);
    }
    50% {
        transform: translate(1px, 1px);
    }
}